<template>
	<view>
		<block v-if="islogin">
			<view style="width: 100%;" :style="'height:' + system_top + 'px;'"></view>
			<view style="width: 100%;height: 42px;" class="row">
				<view v-for="(item,index) in tabbar" :key="index" :class="index == tabIndex ? 'tab_sel' : 'tab_nor'"
					@click="tabbarClick(index)">
					{{item}}
				</view>
			</view>
			<timlist v-if="tabIndex == 0"></timlist>
			<!-- <sysList v-if="tabIndex == 1" @gotoCell="gotoCell"></sysList> -->
			<sysmessageList ref="sysmsglist" v-if="tabIndex == 1" @gotoCell="gotoCell"></sysmessageList>
		</block>
		<block v-else>
			<view class="logintip" @click="login()">{{i18n.mine.logintip}}</view>
		</block>
	</view>

</template>

<script>
	import timlist from '../TUI-Conversation/conversation/conversation.vue'
	// import sysList from "@/components/systemMsg/index.vue"
	import sysmessageList from "@/components/sysmessageList/sysmessageList.vue"
	export default {
		components: {
			timlist,
			sysmessageList
		},
		data() {
			return {
				system_top: 24,
				scrollHeight: 0,
				tabbar: [],
				tabIndex: 0,
				islogin: false
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			this.system_top = getApp().globalData.menuButtonHeight + getApp().globalData.statusBarHeight+ 24
			this.scrollHeight = res.windowHeight - 42 - this.system_top
			this.tabbar = [this.i18n.message.liaotian, this.i18n.message.tongzhi]
		},
		onShow() {
			const value = uni.getStorageSync('userinfo');
			// console.log(value)
			if (value.uid > 0) {
				this.islogin = true
				var that = this
				setTimeout(function() {
					that.$refs.sysmsglist.requestData()
				}, 100);
				this.getUnreadMsg()
			} else {
				this.islogin = false
			}
			
		},
		onLoad() {

		},
		methods: {
			getUnreadMsg() {
				this.http.sendRequest('Message.GetUnreadMessage').then(function(data) {
					if (data.code == 0) {
						var dic = data.info[0]
						if (parseInt(dic.count) > 0) {
							uni.setTabBarBadge({
								index: 2,
								text: dic.count
							})
						} else {
							uni.removeTabBarBadge({
								index: 2
							})
						}

					}
				})
			},
			tabbarClick(index) {
				this.tabIndex = index
			},
			gotoCell(item) {
				console.log(item)
				uni.navigateTo({
					url: '../../package-message/pages/sysMsgList'
				})
			},
			login() {
				uni.navigateTo({
					url: '../login/login'
				})
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: #fff;
	}

	.logintip {
		width: 120px;
		height: 40px;
		border-radius: 20px;
		background-color: #00C1DE;
		color: white;
		font-size: 16px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		margin-top: 300px;
		margin-left: calc(50% - 60px);
	}

	.tab_sel {
		font-size: 16px;
		font-weight: bold;
		color: #00C1DE;
		position: relative;
		height: 40px;
		justify-content: center;
		width: 50%;
		text-align: center;
	}

	.tab_sel::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		height: 2px;
		width: 100%;
		background-color: #00C1DE;
	}

	.tab_nor {
		font-size: 16px;
		font-weight: bold;
		color: #6F6F6F;
		position: relative;
		height: 30px;
		width: 50%;
		text-align: center;
	}
</style>
